
<template>

<div class="table">
    <p class="title">
      成绩详情
    </p>
    <section class="content-el">
    <div>
      <label style="color: #909399;
    font-weight: 500;">课程名称：</label>
    <el-input  placeholder="请输入课程名称" style="width: 200px;margin-right: 100px;" v-model="subject" clearable>
      </el-input>
      <label style="color: #909399;
    font-weight: 500;" >考试日期：</label>
    <el-date-picker
      type="date"
      placeholder="选择日期" style="margin-right: 100px;" v-model="date" value-format="yyyy-MM-dd">
    </el-date-picker>
    <el-button type="primary" 
    @click="search"
    icon="el-icon-search">查询</el-button>
    <el-button type="primary" @click="exportExcel" :loading="loadingExcel" icon="el-icon-download" >导出excel</el-button>

    </div>
      <el-table ref="filterTable" :data="rank" v-loading="loading">
        <el-table-column
          prop="answerDate"
          label="考试日期"
          sortable
          width="200">
        </el-table-column>
        <el-table-column
          prop="subject"
          label="课程名称"
          width="200"
          filter-placement="bottom-end">
          <template slot-scope="scope">
            <el-tag>{{scope.row.subject}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column
          prop="mark"
          label="成绩"
          width="200"
          
          filter-placement="bottom-end">
        </el-table-column>
        
        <el-table-column prop="rank" label="排名" width="200">
          
          <template slot-scope="scope" >
            {{tran(scope.row.rank)}}

            <i class="el-icon-view" @click="showRank"></i>
          </template>
          
        </el-table-column>
        <el-table-column
          label="详情"
          width="200">
          <template slot-scope="scope">
            <el-button @click="showInfo(scope.row.examCode)">查看详情</el-button>
          </template>
          
        </el-table-column>
      </el-table>
      <el-dialog title="详情" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="平时成绩:" label-width="250px">
            <el-input style="width: 200px;" v-model="form.ptScore" disabled ></el-input>
          </el-form-item>
          <el-form-item label="期末成绩:" label-width="250px">
            <el-input style="width: 200px;" v-model="form.etScore" disabled ></el-input>
          </el-form-item>
          <span style="color: red;margin-left: 180px;">*</span>
          <span>平时成绩占比{{String(form.rate).split(":")[0]+'0'}}%,期末成绩占比{{String(form.rate).split(":")[1]+'0'}}%</span>
        </el-form>
      </el-dialog>  
      <el-row type="flex" justify="center" align="middle" class="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="pagination.current"
          :page-sizes="[4,6,8,10]"
          :page-size="pagination.size"
          layout="total, sizes, prev, pager, next, jumper"
          :total="pagination.total">
        </el-pagination>
      </el-row>
    </section>

  </div>

  </template>
  
  
  <script>
  export default {
    data() {
        return {
            loadingExcel:false,
            pagination: { //分页后的留言列表
              current: 1, //当前页
              total: null, //记录条数
              size: 10, //每页条数
            },
            form:[],
            dialogFormVisible:false,
            loading: false, //加载标识符
            error: [], //错题
            filter: null, //过滤参数
            date:'',
            subject:'',
            showRankData:false,
            rank:[]
    }
    },
    created(){
        this.fromTable()
    },
    methods:{
      search(){
        let studentId = this.$jwtDecode(sessionStorage.getItem("token")).id
            this.$axios({
                url:process.env.BASE_API_SCORE+'/rank/'+this.pagination.current+'/'+this.pagination.size+'/'+studentId,
                method:'post',
                data:{
                  studentId:studentId,
                  subject:this.subject,
                  date:this.date
                }
            }).then(res=>{
                this.loading = false //数据加载完成去掉遮罩
                this.rank = res.data.data.records
                
                this.pagination = {...res.data.data}
                let mapVal = this.error.map((element,index) => { //通过map得到 filter:[{text,value}]形式的数组对象
                    let newVal = {}
                    newVal.text = element.answerDate
                    newVal.value = element.answerDate
                    return newVal
                })
                let hash = []
                const newArr = mapVal.reduce((item, next) => { //对新对象进行去重操作
                hash[next.text] ? '' : hash[next.text] = true && item.push(next);
                return item
                }, []);
                this.filter = newArr
            })
      },
      exportExcel(){
      let studentId = this.$jwtDecode(sessionStorage.getItem("token")).id
      this.loadingExcel=true
      this.$axios({
            headers: {
                'Content-Type': 'application/json'
            },
            url:process.env.BASE_API_OTHER+'/export',
            method:'put',
            responseType:'blob',
            data:{
              studentId:studentId,
              subject:this.subject,
              date:this.date
            }
            }).then(res=>{
              this.loadingExcel=false
              let blob = new Blob([res.data], { type: "application/xlsx" });

                let url = window.URL.createObjectURL(blob);
                const link = document.createElement("a"); // 创建a标签
                link.href = url;
                link.download = "我的成绩"+".xlsx"; // 重命名文件
                link.click();
                URL.revokeObjectURL(url);

                this.$message({
                  type:'success',
                  message:'导出成功'
                })
          }).catch(()=>{
            this.loadingExcel=false
            this.$message({
                  type:'error',
                  message:'请求超时'
            })
          })
        },
      showRank(){
          this.showRankData=!this.showRankData;
      },
      tran(rank){
        if(this.showRankData)
          return rank;
        else
          return '***'
      },
      showInfo(examCode){
        let studentId = this.$jwtDecode(sessionStorage.getItem("token")).id
        this.$axios({
            url:process.env.BASE_API_SCORE+'/rank/'+studentId+"/"+examCode,
            method:'get',
        }).then(res=>{
            this.form=res.data.data 
        })


        this.dialogFormVisible=true
      },
       //改变当前记录条数
    handleSizeChange(val) {
      this.pagination.size = val
      this.fromTable()
    },
    //改变当前页码，重新发送请求
    handleCurrentChange(val) {
      this.pagination.current = val
      this.fromTable()
    },
    fromTable(){
          let studentId = this.$jwtDecode(sessionStorage.getItem("token")).id
            this.$axios({
                url:process.env.BASE_API_SCORE+'/rank/'+this.pagination.current+'/'+this.pagination.size+'/'+studentId,
                method:'get',
            }).then(res=>{
                this.loading = false //数据加载完成去掉遮罩
                this.rank = res.data.data.records
                
                this.pagination = {...res.data.data}
                let mapVal = this.error.map((element,index) => { //通过map得到 filter:[{text,value}]形式的数组对象
                    let newVal = {}
                    newVal.text = element.answerDate
                    newVal.value = element.answerDate
                    return newVal
                })
                let hash = []
                const newArr = mapVal.reduce((item, next) => { //对新对象进行去重操作
                hash[next.text] ? '' : hash[next.text] = true && item.push(next);
                return item
                }, []);
                this.filter = newArr
            })
        }
    }
    

  };
  </script>
  
  <style lang="less" scoped>
  
  .pagination {
    padding-top: 20px;
  }
  .table {
    width: 1110px;
    margin: 0 auto;
    .title {
      margin: 20px;
    }
    .content-el {
      background-color: #fff;
      padding: 20px;
    }
  }
  </style>
  


